body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.dashboard-container {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 250px;
  background: #2c3e50;
  color: #ecf0f1;
  overflow-y: auto;
  padding: 20px;
}

.sidebar h2 {
  font-size: 1.2em;
  margin-bottom: 1em;
  text-transform: uppercase;
}

.sidebar nav a,
.sidebar nav .menu-item {
  display: block;
  color: #ecf0f1;
  text-decoration: none;
  padding: 8px 0;
  cursor: pointer;
}

.sidebar nav a:hover,
.sidebar nav .menu-item:hover {
  color: #bdc3c7;
}

.submenu {
  display: none;
  margin-left: 10px;
}

.submenu a {
  padding-left: 15px;
  font-size: 0.95em;
}

.main-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  background: #f8f9fa;
  position: relative;
}

.filter-bar {
  background: #fff;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.filter-bar h3 {
  margin-top: 0;
}

.filter-bar form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}

.filter-group {
  display: flex;
  flex-direction: column;
  min-width: 200px;
}

.filter-group label {
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 0.9em;
}

.filter-group input, 
.filter-group select {
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.filter-actions {
  display: flex;
  align-items: flex-end;
}

.filter-actions button {
  background: #2980b9;
  color: #fff;
  padding: 8px 12px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.filter-actions button:hover {
  background: #1f5f82;
}

.report-section {
  background: #fff;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.report-section h2, .report-section h3 {
  margin-top: 0;
  margin-bottom: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9em;
}

table th, table td {
  padding: 10px;
  border: 1px solid #ddd;
}

table th {
  background: #f0f0f0;
  text-align: left;
}

.subsection {
  margin-top: 20px;
}

.subsection h3 {
  margin-top: 0;
  margin-bottom: 10px;
}

.kpi {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.kpi p {
  background: #eef1f3;
  padding: 10px;
  border-radius: 4px;
  margin: 0;
}

.modal-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(0,0,0,0.4);
  display: none;
  justify-content: center;
  align-items: center;
}

.modal-overlay.active {
  display: flex;
}

.modal {
  background: #fff;
  border-radius: 5px;
  width: 600px;
  max-width: 90%;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  padding: 20px;
  position: relative;
}

.modal h4 {
  margin-top: 0;
}

.modal-close {
  position: absolute;
  top: 10px; right: 10px;
  background: none;
  border: none;
  font-size: 1.2em;
  cursor: pointer;
}

.modal table {
  margin-top: 20px;
}

