/* Pagination.css */
.pagination {
    display: flex;
    list-style: none;
    padding: 1em 0;
    border:  1px solid #ddd;
    background: #fff;
  }
  
  .page-item {
    margin: 0 5px;
    cursor: pointer;
  }
  
  .page-item a {
    padding: 8px 16px;
    text-decoration: none;
    color: #2980b9;
  }
  
  .page-item.active a {
    background-color: #2980b9;
    color: white;
    border-radius: 5px;
  }
  
  .page-item.disabled a {
    color: #6c757d;
    cursor: not-allowed;
  }
  